.swiper {
  background-color: white;
  width: 45%;
  padding-top: 2rem;
  display: flex;
  border-radius: 20px;
  margin-left: auto;
  margin-right: auto;
}

.swiper-slide {
  display: flex;
  flex-direction: column;
  text-align: center;
  font-size: 18px;
  padding: 0 0 3rem 0;

  /* Center slide text vertically */
  display: -webkit-box;
  display: -ms-flexbox;
  display: -webkit-flex;
  -webkit-box-pack: center;
  -ms-flex-pack: center;
  -webkit-justify-content: center;
  justify-content: center;
  -webkit-box-align: center;
  -ms-flex-align: center;
  -webkit-align-items: center;
  align-items: center;
}

.swiper-slide-img {
  display: block;
  width: 70%;
  border-radius: 15px;
  object-fit: cover;
}

.swiper-slide h2 {
  color: #48cae4;
  margin-bottom: 0;
}

.swiper-slide p {
  color: #03045e;
  font-weight: bold;
  padding: 0 1.3rem 0 1.3rem;
  width: 70%;
}

.swiper-pagination div {
  color: red;
}

.swiper-button-next,
.swiper-button-prev {
  color: #00b4d8;
  width: 3rem;
  height: 4rem;
  border-radius: 10px;
  transition: color 0.3s ease-in-out, background-color 0.3s ease-in-out;
  transform: translateY(5rem);
}

.swiper-button-next:hover,
.swiper-button-prev:hover {
  color: white;
  background-color: #00b4d8;
}

.swiper-pagination-bullet {
  background-color: #00b4d8;
}

.tags {
  margin-top: 1rem;
  padding: 0 1rem;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-wrap: wrap;
}

.tags span {
  padding: 1rem;
  margin: 0.6rem;
  border-radius: 20px;
  font-weight: bold;
}

@media (max-width: 1012px) {
  .swiper {
    width: 60%;
    margin-bottom: 2rem;
  }
  .swiper-slide {
    padding-top: 1rem;
  }
}

@media (max-width: 768px) {
  .swiper {
    width: 100%;
  }
  .swiper-slide h2 {
    width: 60%;
  }
  .swiper-slide p {
    width: 60%;
  }
  .tags {
    max-width: 50%;
  }
  .tags span {
    font-size: 0.9rem;
    margin: 0.3rem;
  }
}

@media (max-width: 500px) {
  .swiper-slide h2 {
    font-size: 1.5rem;
    margin-bottom: 1.5rem;
  }
}
